<template>
  <div class="gdt">
    <img
      src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/GdtReality/header.png"
      alt=""
      class="part1" />
    <img
      src="@/assets/GdtReality/part_02.jpg"
      alt=""
      class="part1" />
    <img
      src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/GdtReality/03.png"
      alt=""
      class="part1" />
    <img
      src="@/assets/GdtReality/part_04.jpg"
      alt=""
      class="part1" />
    <img
      src="@/assets/GdtReality/part_05.jpg"
      alt=""
      class="part1" />
    <div class="part6">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请填写手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]" />
        <VanField
          v-model="form.code"
          name="code"
          placeholder="请填写验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <VanCountDown
              ref="countDown"
              class="button"
              millisecond
              :time="60000"
              :time-data="'timeData'"
              :auto-start="false"
              format="ss"
              @finish="finish">
              <template #default="timeData">
                <span
                  class="code"
                  style="text-align: center;"
                  @click="start">{{
                    isFinsh ? "获取验证码" : timeData.seconds + "s"
                  }}</span>
              </template></VanCountDown>
          </template>
        </VanField>
        <div class="btn" @click="submit">
          <div class="submit">立即抢课</div>
        </div>
      </VanForm>
    </div>
    <img
      src="@/assets/GdtReality/part_07.jpg"
      alt=""
      class="part1" />
    <BackDiaLog @backDiaLogSubmit="pay" />
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import BackDiaLog from '@/components/BaiduABBusniess/backDialog' // 支付挽留弹框
import { codeMap } from './ErrorMsg'
export default {
  components: {
    BackDiaLog
  },
  data: function () {
    return {
      form: {},
      isFinsh: true,
      config: {
        show: false
      }
    }
  },
  created () {
    this.form = {
      phone: localStorage.getItem('GdtReality-phone'),
      code: localStorage.getItem('GdtReality-code')
    }
    const needCheckOrder = getSearch('checkOrder')
    // 如果是支付返回的 则检查订单
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    // 提交表单
    submit () {
      // 埋点
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交按钮/底部付费按钮',
        module_part: '106_toufang_lcx_0163'
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess()
        }
        this.$log.click(codeMap[err.code].report)
      })
    },
    // 跳转成功页面
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    // 查询订单
    checkOrder () {
      if (!this.form.phone) return
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((res) => {
          // 未支付
          console.log(res)
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', false)
          if (code === 30006) {
            this.goSucccess()
          } else {
            this.showMsg(err)
          }
        })
    },
    // 获取验证码
    start () {
      if (!this.isFinsh) {
        return
      }
      // 埋点
      this.$log.click({
        current_page: location.href,
        module_ori: '获取验证码',
        module_part: '106_toufang_lcx_0162'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          this.$refs.countDown.start()
          this.isFinsh = false
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 倒计时结束
    finish (e) {
      this.$refs.countDown.reset()
      this.isFinsh = true
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      this.$apis.userOperator
        .phoneCheckCode(this.form)
        .then(async (res) => {
          localStorage.setItem('GdtReality-phone', this.form.phone)
          localStorage.setItem('GdtReality-code', this.form.code)
          this.pay()
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 支付
    pay () {
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: 2,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((data) => {
          this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
          window.location.replace(decodeURIComponent(data.url))
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }

  }
}
</script>

<style lang="scss" scoped>
.gdt {
  img {
    width: 100%;
    vertical-align: top;
  }

  .part6 {
    background-image: url("~@/assets/GdtReality/part_06.jpg");
    background-size: 100% auto;
    height: 380px;
    box-sizing: border-box;
    padding: 20px 60px;
    text-align: center;

    .wrap {
      position: relative;
    }

    .btn {
      .submit {
        width: 650px;
        height: 100px;
        background: #fa6c67;
        border-radius: 3px;
        font-size: 40px;
        font-family: Source Han Sans CN;
        font-weight: bold;
        color: #fff;
        line-height: 100px;
      }
    }

    .code {
      display: inline-block;
      width: 229px;
      height: 100%;
      text-align: center;
      border-left: rgba(0, 0, 0, 0.2) solid 1px;
      font-size: 32px;
      font-family: Source Han Sans CN;
      font-weight: 500;
      color: #eb3f3c;
      line-height: 32px;
    }

    /deep/ .van-cell {
      margin-bottom: 20px;
      width: 650px;
      height: 86px;
      background: #fff;
      border: 1px solid #626261;
      border-radius: 3px;
      font-size: 33px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;

      &::after {
        display: none;
      }
    }
  }
}
</style>
