<template>
  <div class="gdt">
    <img
      src="@/assets/GDTcartoon-zjy/part_01.jpg"
      alt=""
      class="part1" />
    <img
      src="@/assets/GDTcartoon-zjy/part_02.jpg"
      alt=""
      class="part1" />
    <img
      src="@/assets/GDTcartoon-zjy/part_03.jpg"
      alt=""
      class="part1" />
    <img
      src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/GdtNew/part_04.jpg"
      alt=""
      class="part1" />
    <img
      src="@/assets/GDTcartoon-zjy/part_05.jpg"
      alt=""
      class="part1" />
    <div class="part6">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        :show-error="false"
        validate-first
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请填写手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]" />
        <div class="wrap">
          <VanField
            class="phonecode"
            v-model="form.code"
            name="code"
            placeholder="请填写验证码"
            :rules="[{ required: true, message: '33005' }]">
          </VanField>
          <VanCountDown
            ref="countDown"
            class="button"
            millisecond
            :time="60000"
            :time-data="'timeData'"
            :auto-start="false"
            format="ss"
            @finish="finish">
            <template #default="timeData">
              <span
                class="code"
                style="text-align: center;"
                @click="start">{{
                  isFinsh ? "获取验证码" : timeData.seconds + "s"
                }}</span>
            </template></VanCountDown>
        </div>
        <div class="btn" @click="submit">
          <div class="submit">立即抢课</div>
        </div>
      </VanForm>
    </div>
    <img
      src="@/assets/GDTcartoon-zjy/part_07.jpg"
      alt=""
      class="part1" />
    <BackDiaLog @backDiaLogSubmit="pay" />
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import BackDiaLog from '@/components/BaiduABBusniess/backDialog' // 支付挽留弹框
import { codeMap } from './ErrorMsg'
export default {
  components: {
    BackDiaLog
  },
  data: function () {
    return {
      form: {},
      isFinsh: true,
      config: {
        show: false
      }
    }
  },
  created () {
    this.form = {
      phone: localStorage.getItem('GDTcartoon-zjy-phone'),
      code: localStorage.getItem('GDTcartoon-zjy-code')
    }
    const needCheckOrder = getSearch('checkOrder')
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    submit () {
      // 埋点
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交按钮/底部付费按钮',
        module_part: '106_toufang_lcx_0174'
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      console.log(err)
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess()
        }
        this.$log.click(codeMap[err.code].report)
      })
    },
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    checkOrder () {
      if (!this.form.phone) return
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((res) => {
          // 未支付
          console.log(res)
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', false)
          if (code === 30006) {
            this.goSucccess()
          } else {
            this.showMsg(err)
          }
        })
    },
    // 获取验证码
    start () {
      if (!this.isFinsh) {
        return
      }
      // 埋点
      this.$log.click({
        current_page: location.href,
        module_ori: '获取验证码',
        module_part: '106_toufang_lcx_0173'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          this.$refs.countDown.start()
          this.isFinsh = false
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 错误表单提示
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    finish (e) {
      this.$refs.countDown.reset()
      this.isFinsh = true
    },
    // 选择支付类型
    pay () {
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: 2,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((data) => {
          this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
          window.location.replace(decodeURIComponent(data.url))
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    },
    onSubmit (e) {
      // 校验手机号和验证码
      this.$apis.userOperator
        .phoneCheckCode(this.form)
        .then(async (res) => {
          localStorage.setItem('GDTcartoon-zjy-phone', this.form.phone)
          localStorage.setItem('GDTcartoon-zjy-code', this.form.code)
          this.pay()
        })
        .catch((err) => {
          this.showMsg(err)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.gdt {
  img {
    width: 100%;
    vertical-align: top;
  }

  .part6 {
    background: #fffbe8;
    height: 380px;
    box-sizing: border-box;
    padding: 20px 60px;
    text-align: center;

    .wrap {
      position: relative;
    }

    .btn {
      .submit {
        width: 630px;
        height: 74px;
        background: linear-gradient(90deg, #ff6539, #ffbd48);
        border-bottom: 2px solid #fff;
        border-right: 2px solid #fff;
        box-shadow: 6px 12px 0 0 #ff5252;
        border-radius: 26px;
        font-size: 48px;
        font-family: SourceHanSansSC;
        font-weight: 500;
        color: #fff;
        line-height: 74px;
      }
    }

    .code {
      display: inline-block;
      width: 198px;
      height: 73px;
      position: relative;
      top: -10px;
      line-height: 73px;
      background: linear-gradient(90deg, #ff6539, #ffbc48);
      border-bottom: 2px solid #fff;
      border-right: 2px solid #fff;
      box-shadow: 6px 12px 0 0 #ff5252;
      border-radius: 24px;
      font-size: 33px;
      font-weight: 500;
      color: #fff;
    }

    .button {
      width: 230px;
      height: 88px;
      vertical-align: top;
      line-height: 88px;
      padding-left: 20px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      font-size: 38px;
      font-family: Source Han Sans CN;
      font-weight: 400;
      background: #fffbe8;
    }

    /deep/ .van-cell {
      &.phonecode {
        width: 400px;
      }

      margin-bottom: 20px;
      width: 632px;
      height: 86px;
      background: #fff;
      box-shadow: inset 0 5px 4px 0 rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      font-size: 33px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;

      &::after {
        display: none;
      }
    }
  }
}
</style>
